import { defineStore } from 'pinia'
import request from 'utils/request'

export const useBuybackShipping = defineStore('buybackShipping', {
  state: () => ({
    shippingInfo: {
      isLoading: false,
      modal: false,
      result: undefined
    },
    additionalShipping: {
      isLoading: false,
      modal: false,
      result: undefined
    },
    shippingKit: {
      isLoading: false,
      modal: false,
      result: undefined
    }
  }),
  actions: {
    async $createMultipleLabels(payload = { id: undefined, data: undefined }) {
      const response = await request({
        url: `/v1/pos/order/shipping-label/generate-multiple/${payload.id}`,
        method: 'post',
        data: payload.data
      })
      this.additionalShipping.result = response
    },
    async $actionCheckCustomTracking(payload) {
      return await request({
        url: `/v1/pos/order/custom-tracking/check-courier?shipping_tracking=${payload}`,
        method: 'get'
      })
    },
    async $actionSaveCustomTracking(payload) {
      return await request({
        url: `/v1/pos/order/custom-tracking/${payload.id}`,
        method: 'post',
        data: payload.data
      })
    },
    async $downloadShippingKit(payload = { id: undefined, params: undefined }) {
      return await request({
        url: `/v1/shipping/kit/print/${payload.id}`,
        method: 'get',
        params: payload.params,
        responseType: 'arraybuffer'
      })
    },
    async $downloadBulkShippingKit(payload = { id: undefined, params: undefined }) {
      return await request({
        url: `/v1/pos/order/shipping-label/print-batch/${payload.id}`,
        method: 'post',
        params: payload.params,
        responseType: 'arraybuffer'
      })
    }
  }
})