<script setup>
import { computed, defineProps, reactive, defineEmits, watch, ref } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'
import { ElMessage } from 'element-plus'
import { useVuelidate } from '@vuelidate/core'
import { required, minValue } from "@vuelidate/validators"
import { useManageShipping } from 'composable/setting'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Alert from 'bbUi/components/alert/alert.vue'

const store = useBuybackShipping()
const props = defineProps({
  id: '-',
  total: 0,
  shippings: {
    type: Array,
    default: () => []
  },
  shippingLabelStatus: {
    type: String,
    default: ''
  },
  offerType: {
    type: String,
    default: 'Mail-In'
  }
})
const emits = defineEmits([
  'created',
  'failed'
])
const manageShipping = useManageShipping()

const data = reactive({
  add_shipping_label_number: 1
})
const rules = ref({})
const previews = computed(() => {
  if (data.add_shipping_label_number >= 4) return 3
    else if (data.add_shipping_label_number > 1) return data.add_shipping_label_number
      else return 1
})
const validation = useVuelidate({
  add_shipping_label_number: { required, minValue: minValue(1) },
}, data, {
  $lazy: true
})
const availableLabelLeft = computed(() => {
  return ((rules?.value?.label_qty ?? 1) - (props.shippings?.length ?? 1)) - data.add_shipping_label_number
})

const onChangeTotal = (e) => {
  data.add_shipping_label_number = Number(e?.target?.value)
}
const onClose = () => {
  data.add_shipping_label_number = 1
  store.additionalShipping.modal = false
}
const onCreate = async () => {
  const isValid = await validation.value.$validate()
  if (!isValid) return

  await tryCatchRequest(
    store.additionalShipping,
    async () => {
      await store.$createMultipleLabels({
        id: props.id,
        data
      }).then(() => {
        ElMessage({
          message: store.additionalShipping?.result?.message,
          type: 'info'
        })
        emits('created')
        onClose()
      }).catch(() => {
        emits('failed')
      })
    }
  )
}

watch(() => store.additionalShipping.modal, async (val) => {
  if (!!val) {
    store.additionalShipping.isLoading = true
    await manageShipping.actionAll(true, (res) => {
      const rulesLabels = res.param?.shipping_rules_multiple_label ?? []
      if (rulesLabels.length) {
        const selected = rulesLabels.find(i => i.offer_type === props.offerType)
        rules.value = {
          ...(selected ?? {}),
          label_qty: Number(selected?.label_qty) ?? 1
        }
      } else rules.value = undefined
    })
    store.additionalShipping.isLoading = false
  }
})
</script>

<template>
  <Modal
    v-model="store.additionalShipping.modal"
    size="xs"
    @close="onClose">
    <template #header>
      <h5 class="text-2xl font-medium">
        Additional Shipping Info
      </h5>
    </template>
    <template #default>
      <div class="space-y-8">
        <Alert color="blue" view="smooth" dense>
          <div class="flex items-center gap-x-4">
            <span class="material-icons" style="line-height: 1">info</span>
            <p>Labels will be created using the first shipping data</p>
          </div>
        </Alert>
        <div class="space-y-6">
          <div>
            <Input
              v-model="data.add_shipping_label_number"
              :max="15"
              :disabled="store.additionalShipping.isLoading"
              :view="validation.add_shipping_label_number.$error ? 'danger' : 'default'"
              type="number"
              block
              ltr
              @change="onChangeTotal">
              <template #label>
                <span class="font-medium">Number of Additional Labels</span>
              </template>
            </Input>
            <p>
              <small
                v-for="(error, n) in validation.add_shipping_label_number.$errors"
                :key="n"
                class="text-red-04">
                {{ error.$message }}
              </small>
            </p>
            <small class="text-grey-02 text-xs">
              {{ availableLabelLeft < 0 ? 0 : availableLabelLeft }}
              {{ availableLabelLeft > 1 ? `labels` : `label` }}
              left to generate.
            </small>
          </div>
          <div v-if="shippingLabelStatus == 1">
            <div
              rows="8"
              class="block w-full border border-grey-01 rounded-md px-4 py-4 text-grey-02">
              <div class="space-y-6">
                <h6 class="font-medium text-lg text-grey-04">Preview:</h6>
                <div class="space-y-2">
                  <div>
                    <span class="font-medium text-grey-04">Base Tracking:</span>
                    {{ props.shippings[0]?.shipping_tracking ?? '-' }}
                  </div>
                  <p>New labels will have tracking numbers like:</p>
                  <div>
                    <p
                      v-for="n in previews"
                      :key="n">
                      {{
                        props.shippings[0]?.shipping_tracking
                          ? `${props.shippings[0]?.shipping_tracking}-${n}`
                          : `-${n}`
                      }}
                      {{ previews === n && previews > 1 ? '.etc' : '' }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template #footer>
      <div class="flex items-center justify-between gap-4">
        <Button
          :disabled="store.additionalShipping.isLoading"
          color="white"
          @click="onClose">
          Cancel
        </Button>
        <Button
          :disabled="store.additionalShipping.isLoading || availableLabelLeft < 0"
          :loading="store.additionalShipping.isLoading"
          color="teal"
          @click="onCreate">
          Create Labels
        </Button>
      </div>
    </template>
  </Modal>
</template>
