<template>
  <div class="grid grid-cols-2 gap-6">
    <div>
      <Input v-model="data.shipping_firstname" label="First Name" block :view="validation.shipping_firstname.$invalid && 'danger' || 'default'" />
      <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_firstname.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div>
      <Input v-model="data.shipping_lastname" label="Last Name" block />
      <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_lastname.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-2" v-if="isEasyOffer">
      <Input v-model="data.company_name" label="Company Name" block />
    </div>
    <div class="col-span-2">
      <Input v-model="data.shipping_address_text" label="Street Address" block v-gmap="address" />
      <small>Street address, P.O box, company name, c/o.</small>
      <small class="text-red-base block" v-for="(error, errorIndex) of validation.valid.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-2">
      <Input v-model="data.shipping_suite" block />
      <small>Apartment, suite, unit, building, floor, etc.</small>
    </div>
    <div class="col-span-2">
      <!-- <Input v-model="data.shipping_phone" label="Phone" block /> -->
      <InputPhone ref="refInputPhone" v-model="data.shipping_phone" label="Phone" block country-autofill-setting />
      <small class="text-red-base block" v-for="(error, errorIndex) of validation.shipping_phone.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div v-if="data.custom_fields && isEasyOffer" v-for="[label, value] in Object.entries(data.custom_fields)" :key="label" class="col-span-2">
  <Input v-model="data.custom_fields[label]" :label="label" block />
</div>

  </div>
</template>
  
<script>
import { defineComponent, reactive, watch, ref } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { helpers } from '@vuelidate/validators'
import { required, validAddress } from '@/validations/index'
import { phoneNumber } from 'utils/number'
import Input from 'bbUi/components/input/input.vue'
import InputPhone from 'components/InputPhone.vue'

export default defineComponent({
  name: 'ShippingLabelInfo',
  components: {
    Input,
    InputPhone
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        shipping_firstname: null,
        shipping_lastname: null,
        shipping_address1: null,
        shipping_suite: '',
        shipping_city: null,
        shipping_country: null,
        shipping_state: null,
        shipping_zip: null,
        shipping_phone: null,
        shipping_address_text: null,
        company_name: null,
        custom_fields: null,
      })
    },
    isEasyOffer: {
      type: Boolean,
      default: false
    },
  },
  emits: ['update:data'],
  setup(props, {emit}) {
    const data = reactive(props.data)
    const refInputPhone = ref(null)

    Object.assign(data, {
       valid: data.shipping_address_text && data.shipping_address_text !== '' && data.shipping_address_text !== null
    })
    const rules = {
      shipping_firstname: {
        required
      },
      shipping_lastname: {
        required
      },
      shipping_phone: {
        required,
        invalidNumber: helpers.withMessage('Phone number is invalid', () => !data.shipping_phone ? true : refInputPhone.value?.isValid)
      },
      valid: {
        sameAs: validAddress,
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    watch(() => ({...data}), () => {
      emit('update:data', data)
    })
    return {
      phoneNumber,
      data,
      refInputPhone,
      validation,
      validate,
      address: {
        valid: 'valid',
        route: 'shipping_address1',
        locality: 'shipping_city',
        administrative_area_level_1: 'shipping_state',
        formatted_address: 'shipping_address_text',
        postal_code: 'shipping_zip',
        country: 'shipping_country',
        disallowCustomAddress: true,
        subpremise: 'shipping_suite'
      }
    }
  }
})
</script>