<script setup>
// This is a temporary component for the task: https://app.clickup.com/t/86cz0n5h5
// This component is needed because four features (Mail-In, Bulk Quote, Easy Offer) will be affected.
// It helps to handle the shared logic in one place to avoid repetitive work.
// Note: The component might be refactored later due to current complexity and because we plan to fully migrate to Pinia.

import { defineProps, defineEmits, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'

import Button from 'bbUi/components/button/button.vue'
import BuybackShippingInformation from './BuybackShippingInformation.vue'
import BuybackShippingAdditional from './BuybackShippingAdditional.vue'

const route = useRoute()
const store = useBuybackShipping()
const props = defineProps({
  id: '-',
  multiple: false,
  total: 0,
  shippings: {
    type: Array,
    default: () => []
  },
  shippingLabelStatus: {
    type: String,
    default: ''
  },
  regenerateShippingLabel: {
    type: Function,
    default: () => {}
  }
})
const emits = defineEmits([
  'succeed',
  'failed'
])
const offerType = computed(() => {
  if (route?.name?.toLowerCase().includes('bulkquote')) {
    return 'Bulk Offer'
  }
  if (route?.name?.toLowerCase().includes('easyoffer')) {
    return 'Easy Offer'
  }

  return 'Mail-In'
})

const onDropdownSelect = (type) => {
  if (type == 1) store.shippingInfo.modal = true
    else if (type == 2) store.additionalShipping.modal = true
}
const onCreatedBuybackShippingAdditional = () => {
  emits('succeed')
}
const onFailedBuybackShippingAdditional = () => {
  emits('failed')
}
const onCreateCustomTracking = () => {
  emits('succeed')
}
</script>

<template>
  <Dropdown
    trigger="click"
    placement="bottom-end"
    hide-on-click
    @command="onDropdownSelect">
    <!-- :loading="loading" -->
    <Button
      color="teal"
      view="outlined"
      dense
      icon
      class="btn-action">
      <span class="material-icons">more_horiz</span>
    </Button>
    <template #dropdown>
      <DropdownMenu>
        <DropdownItem command="1">
          <div class="flex items-center gap-2 text-base">
            <span class="material-icons">local_shipping</span>
            Shipping Information
          </div>
        </DropdownItem>
        <DropdownItem command="2">
          <div class="flex items-center gap-2 text-base">
            <span class="material-icons">add</span>
            Additional Shipping Label
          </div>
        </DropdownItem>
      </DropdownMenu>
    </template>
  </Dropdown>

  <BuybackShippingInformation
    :id="props.id"
    :shippings="props.shippings"
    :shippingLabelStatus="props.shippingLabelStatus"
    :regenerateShippingLabel="props.regenerateShippingLabel"
    @saved="onCreateCustomTracking"
  />
  <BuybackShippingAdditional
    :id="props.id"
    :total="props.total"
    :shippings="props.shippings"
    :shippingLabelStatus="props.shippingLabelStatus"
    :offer-type="offerType"
    @created="onCreatedBuybackShippingAdditional"
    @failed="onFailedBuybackShippingAdditional"
  />
</template>

