<script setup>
import { defineProps, defineEmits, ref, watch } from 'vue'
import { tryCatchRequest } from '@/utils/request'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'
import CopyText from 'utils/copy'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

const store = useBuybackShipping()
const props = defineProps({
  id: '-',
  shippings: {
    type: Array,
    default: () => []
  },
  shippingLabelStatus: {
    type: String,
    default: ''
  },
  regenerateShippingLabel: {
    type: Function,
    default: () => {}
  }
})
const emits = defineEmits([
  'saved'
])
const courrier = ref({})
const shippings = ref(props.shippings)

const onClose = () => {
  store.shippingInfo.modal = false
  courrier.value = {}
  shippings.value = {}
}
const onSave = async (item) => {
  item.isLoading = true
  await tryCatchRequest(
    store.shippingInfo,
    () => store.$actionCheckCustomTracking(item.shipping_tracking)
      .then((res) => {
        const [data] = res
        courrier.value = data
      })
      .catch(() => {
        courrier.value = {}
      })
  )
  if (courrier.value?.courier_code && courrier.value?.courier_name) {
    const payload = {
      id: props.id,
      data: {
        order_shipping_label_id : item.order_shipping_label,
        shipping_tracking: item.custom_tracking,
        courier_code: courrier?.value?.courier_code,
        courier_name: courrier?.value?.courier_name,
        force_courier_code : 0
      }
    }
    await tryCatchRequest(
      store.shippingInfo,
      () => store.$actionSaveCustomTracking(payload)
        .then(() => {
          emits('saved')
          onClose()
        })
        .catch(() => {
          courrier.value = {}
        })
    )
  }
  item.isLoading = false
}

watch(() => props.shippings, (newShippings) => {
  if (newShippings && newShippings.length > 0) {
    shippings.value = newShippings.map(shipping => ({
      ...shipping,
      isLoading: false
    }))
  }
}, { 
  deep: true, 
  immediate: true
})

watch(() => store.shippingInfo.modal, () => {
  if (store.shippingInfo.modal) {
    shippings.value = props.shippings.map(shipping => ({
      ...shipping,
      isLoading: false
    }))
  } else shippings.value = []
})

</script>

<template>
  <Modal
    v-model="store.shippingInfo.modal"
    size="sm"
    hide-footer
    @close="onClose">
    <template #header>
      <h5 class="text-2xl font-medium">
        Shipping Information
      </h5>
    </template>
    <template #default>
      <template v-if="shippingLabelStatus !== '1'">
        <div class="border border-grey-01 rounded-xl p-6 w-full mb-6">
          <div class="flex items-center gap-2 mb-4">
            <i class="material-icons text-red-base icon-md">warning</i>
            <p class="text-2xl font-bold">Regenerate Shiping Label</p>
          </div>
          <p>Shipping label fails to generates. Once the root cause is resolved, you can use this option to regenerate both the shipping label and shipping kit. Once regenerated, the new tracking number and shipping kit will be automatically attached to the offer. </p>
          <Button class="btn btn-teal btn-outlined mt-6" rounded dense @click="regenerateShippingLabel">
            <div class="flex items-center gap-2 px-2 py-1">
              <i class="material-icons text-base">refresh</i>
              <span class="font-medium">Regenerate Shiping Label</span>
            </div>
          </Button>
        </div>
      </template>
      <div class="space-y-4">
        <div
          v-for="(shipping, n) in shippings"
          :key="n"
          class="border border-grey-01 rounded-xl p-5 space-y-4">
          <div class="flex items-center justify-between">
            <h5 class="font-medium text-xl">{{ `Shipping Label ${shipping.order_shipping_label}` }}</h5>
            <Button
              :disabled="!shipping?.custom_tracking || !shipping?.custom_tracking.length"
              :loading="shipping.isLoading"
              view="outlined"
              color="teal"
              dense
              @click="onSave(shipping)">
              Save
            </Button>
          </div>
          <div class="grid grid-cols-2 gap-4">
            <div class="flex items-end gap-x-1">
              <Input
                v-model="shipping.shipping_tracking"
                disabled
                block>
                <template #label>
                  <span class="font-medium">
                    Tracking Number
                  </span>
                </template>
              </Input>
              <Button
                color="teal"
                view="outlined"
                icon
                class="btn-action w-16"
                :style="{
                  paddingRight: 0,
                  paddingLeft: 0
                }"
                @click="CopyText(shipping?.shipping_tracking)">
                <span class="material-icons">content_copy</span>
              </Button>
            </div>
            <div class="grid grid-cols-2 gap-4">
              <div class="flex items-end gap-x-1">
                <Input
                  v-model="shipping.shipping_tracking"
                  disabled
                  block>
                  <template #label>
                    <span class="font-medium">
                      Tracking Number
                    </span>
                  </template>
                </Input>
                <Button
                  color="teal"
                  view="outlined"
                  icon
                  class="btn-action w-16"
                  :style="{
                    paddingRight: 0,
                    paddingLeft: 0
                  }"
                 @click="CopyText(shipping?.shipping_tracking)">
                  <span class="material-icons">content_copy</span>
                </Button>
              </div>
              <div>
                <Input
                  v-model="shipping.custom_tracking"
                  :disabled="shipping.isLoading"
                  block>
                  <template #label>
                    <span class="font-medium">
                      Custom Tracking Number
                    </span>
                  </template>
                </Input>
              </div>
              <div class="flex justify-between">
                <div>
                  <p class="block font-medium">Date Created</p>
                  <span class="text-grey-02">{{ shipping?.created_at }}</span>
                </div>
                <div>
                  <p class="block font-medium">Label Status</p>
                  <span class="text-grey-02">{{ shipping.shipping_label_status ? 'Available' : 'Unavailable' }}</span>
                </div>
              </div>
              <div class="mx-auto">
                  <p class="block font-medium">Shipment Status</p>
                  <span class="text-grey-02">{{ shipping.shipping_status_summary || 'No Shipping Information' }}</span>
                </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </Modal>
</template>
