    //   }
    //   const currentStatusDetail = statuses.value.find(
    //     (status) => status.id === currentStatus.value.id
    //   )
    //   console.log(currentStatusDetail)
    //   if (!currentStatusDetail) return statusNext
    //   const { step_bulk: currentStatusStep } = currentStatusDetail
    //   const nextStatusStep = currentStatusStep + 1
    //   const nextStatusDetail = statuses.value.find(
    //     (status) => status.step_bulk === nextStatusStep
    //   )
    //   if (!nextStatusDetail) return statusNext
    //   return {
    //     id: nextStatusDetail.id || null,
    //     name: nextStatusDetail.short_name || null,
    //     step: nextStatusStep
    //   }
    // })

    const handleUpdateStatus = async (confirmed = false) => {
      modalFormUpdateStatus.value = true
    }

    const handleConfirmUpdateStatus = () => {
      modalFormUpdateStatus.value = false
