<script setup>
import { ref, computed, defineProps, watch, nextTick } from 'vue'
import { useBuybackShipping } from 'store/pinia/buyback/shipping'
import { tryCatchRequest } from '@/utils/request'
import { anchorPrint, anchorDownload } from 'utils/element'
import { useStore } from 'vuex'
import { ElMessage } from 'element-plus'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import BTable from 'components/BTable/BTable.vue'

const storeVuex = useStore()
const store = useBuybackShipping()
const props = defineProps({
  id: '-',
  shippings: {
    type: Array,
    default: () => []
  }
})
const selected = ref([])
const headers = ref([
  {
    key: 'order_shipping_label',
    column_name: 'Label',
    meta: {}
  },
  {
    key: 'shipping_tracking',
    column_name: 'Tracking Number',
    meta: {}
  },
  {
    key: 'shipping_label_status',
    column_name: 'Label Status',
    meta: {}
  },
  {
    key: 'action',
    column_name: '',
    meta: {}
  }
])
const items = computed(() => {
  return props?.shippings ?? []
})
watch(items, (newItems) => {
  if (newItems && newItems.length > 0) {
    nextTick(() => {
      selected.value = [...newItems]
    })
  }
}, { immediate: true })

watch(() => store.shippingKit.modal, (isOpen) => {
  if (isOpen && items.value.length > 0) {
    nextTick(() => {
      selected.value = [...items.value]
    })
  }
})

const downloadingBatch = computed(() => 
  storeVuex.state?.shipping?.processing?.loadingDownload
)

const onClose = () => {
  store.shippingKit.modal = false
}
const onDownload = async (item) => {
  await tryCatchRequest(
    store.shippingKit,
    async () => {
      await store.$downloadShippingKit({
        id: props.id,
        params: {
          order_shipping_label_id: item.order_shipping_label
        }
      }).then(res => {
        anchorPrint(res)
      })
    }
  )
}
const onNotify = () => {
  ElMessage({
    message: 'Download in progress... Feel free to navigate away or close the window',
    type: 'info'
  })
  storeVuex.dispatch('notification/closeNotification', 'NotificationShippingKitDownload', { root: true })
  storeVuex.commit('shipping/SET_PROCESSING_DOWNLOAD_SHIPPING_KIT_PROGRESS', {
    progress: 1,
    path: null,
    type: 'zip',
    loading: true
  }, { root: true })
  storeVuex.commit(
    'notification/OPEN_NOTIFICATION',
    'NotificationShippingKitDownload',
    { root: true }
  )
  storeVuex.commit('notification/SET_NOTIFICATION_CLOSABLE', {
    name: 'NotificationShippingKitDownload',
    closable: false
  }, { root: true })
}
const onBulkDownload = async () => {
  await tryCatchRequest(
    store.shippingKit,
    async () => {
      await store.$downloadBulkShippingKit({
        id: props.id,
        params: {
          order_shipping_label_id: items.value.map(i => i.order_shipping_label)
        }
      }).then(() => {
        onNotify()
      })
    }
  )
}

</script>

<template>
  <Modal
    v-model="store.shippingKit.modal"
    size="md"
    hide-footer
    @close="onClose">
    <template #header>
      <h5 class="text-2xl font-medium">
        Shipping Kit Download
      </h5>
    </template>
    <template #default>
      <div
        v-loading="store.shippingKit.isLoading"
        class="space-y-4">
        <Transition name="fade">
          <Button
            v-show="!!selected?.length"
            view="outlined"
            color="teal"
            dense
            @click="onBulkDownload">
            <div class="flex items-center justify-center gap-x-4">
              <span class="material-icons">
                print
              </span>
              <span>Bulk Download Shipping Kit</span>
            </div>
          </Button>
        </Transition>
        <BTable
          v-model:selected="selected"
          :headers="headers"
          :items="items"
          item-value="order_shipping_label"
          :selectable="items.length > 1 ? true : false">
          <template #header="{ header }">
            <th v-if="header.key !== 'action'" class="flex-width">{{ header.column_name }}</th>
            <th v-else class="flex-width" style="min-width: 80px;">{{ header.column_name }}</th>
          </template>
          <template #column="{ row, key, preventRowClick, resetRowClick }">
            <td v-if="key === 'shipping_label_status'">
              <span v-if="!!row?.shipping_tracking">{{ 'Available'}}</span>
              <span v-else>{{ 'Unavailable'}}</span>
            </td>
            <td v-else-if="key === 'action'" class="w-10">
              <a
                :class="downloadingBatch ? 'opacity-50 cursor-not-allowed' : 'opacity-100'"
                color="white"
                @click.prevent="onDownload(row)">
                <span class="material-icons">
                  print
                </span>
              </a>
            </td>
            <td
              v-else
              @mouseenter="preventRowClick"
              @mouseleave="resetRowClick">
              {{ row[key] }}
            </td>
          </template>
        </BTable>
      </div>
    </template>
  </Modal>
</template>
