<template>
  <div>
    <template v-if="!showWarning">
      <Modal
        v-model="show"
        title="Update Status"
        size="xs"
        class="">
        <template #default>
          <Fieldset
            v-loading="loading"
            class="rounded-md">
            <template #label>Choose Status</template>
            <ul v-if="!!listStatus?.length" class="border-grey-01 border">
              <li v-for="(item, itemN) in listStatus" :key="itemN">
                <div
                  :class="[
                    'px-2 py-1.5 text-center',
                    (!item.disabled && !item.active && !loadingUpdate) && 'hover:bg-teal-01 hover:bg-opacity-50 cursor-pointer text-grey-02',
                    (!item.disabled && !!item.active && !loadingUpdate) && 'bg-teal-02 bg-opacity-40 cursor-pointer',
                    (!!item.disabled || !!loadingUpdate) && 'bg-teal-01 bg-opacity-30 cursor-not-allowed text-grey-01'
                  ]"
                  @click="handleSelectStatus(item)">
                  <Tooltip
                    :placement="'top'"
                    :disabled="!item.disabled">
                    <template #content>
                      <div>
                        <p>Quote must be signed first to unlock this status</p>
                      </div>
                    </template>
                    <p>
                      {{ item.status_name }}
                    </p>
                  </Tooltip>
                </div>
                <hr v-if="itemN !== (listStatus.length - 1)" class="my-0 border-grey-01" />
              </li>
            </ul>
          </Fieldset>
        </template>
        <template #footer>
          <div class="flex items-center justify-end">
            <Button
              color="teal"
              :disabled="!selected || selected?.id == status?.id"
              :loading="loadingUpdate"
              @click="handleSave">
              Update Status
            </Button>
          </div>
        </template>
      </Modal>
    </template>
    <Modal v-model="showWarning" :class="showNotesWarning && 'hidden'" size="xs" hide-header hide-footer>
      <template #default>
        <div v-if="warningType == 0" class="flex flex-col gap-6 items-center my-6">
          <img src="@/assets/icon/warning.svg" />
          <h6 class="text-3xl font-medium">Proceed With Quote?</h6>
          <p class="text-grey-02 text-center">
            Ensure the payout is correct, then make sure to send the Customer Portal link to the customer for contract access and review.
          </p>
          <div class="flex flex-wrap items-center justify-center gap-14">
            <a
              href=""
              @click.prevent="handleCancelUpdate"
              class="px-6 py-4 hover:text-teal-03">
              Cancel
            </a>
            <Button
              :loading="loading"
              color="teal"
              @click="handleUpdateStatus"
              >
              Yes, proceed
            </Button>
          </div>
        </div>
        <div v-if="warningType == 1" class="flex flex-col gap-6 items-center my-6">
          <img src="@/assets/icon/warning.svg" />
          <h6 class="text-3xl font-medium">Change Status?</h6>
          <p class="text-grey-02 text-center">
            Your reconciled quantity will be reset to the original quoted quantity.
          </p>
          <div class="flex flex-wrap items-center justify-center gap-14">
            <Button
              :loading="loading"
              color="teal"
              @click="handleUpdateStatus">
              Yes, change status
            </Button>
            <a
              href=""
              @click.prevent="handleCancelUpdate"
              class="px-6 py-4 hover:text-teal-03">
              Cancel
            </a>
          </div>
        </div>
      </template>
    </Modal>
    <BuybackNotesWarning @proceed="handleUpdateStatus" />
  </div>
</template>

<script>
import { defineComponent, computed, ref, watch } from 'vue'
import { useStore } from 'vuex'
import { useShowNote } from 'composable/buyback'
import { useBuybackNotes } from  '@/store/pinia/buyback/notes'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import BuybackNotesWarning from '@/components/Features/Buyback/BuybackNotes/BuybackNotesWarning.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'BulkQuoteDetailFormStatus',
  components: {
    Modal,
    Button,
    Fieldset,
    BuybackNotesWarning
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    quoteId: {
      type: String,
      default: undefined,
      required: true
    },
    status: {
      type: Object,
      default: undefined
    },
    order: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue', 'updateStatus'],
  setup(props, ctx) {
    const storePiniaNotes = useBuybackNotes()
    const store = useStore()
    const loading = ref(false)
    const loadingUpdate = ref(false)
    const show = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const showWarning = ref(false)
    const warningType = ref(0)
    const listStatus = ref([])
    const selected = ref(undefined)

    const isSignedContract = computed(() => props?.order?.is_signature_exist !== 0)
    const isReconciledExist = ref(false)

    const reviewed = ref(false)
    const showNotesWarning = ref(false)
    const { state: notes, actionAll: showNotes } = useShowNote('online')
    const handleGetStatus = async () => {
      loading.value = true
      loadingUpdate.value = true
      try {
        const [statusResponse, info] = await Promise.all([
          store.dispatch('bulkQuote/getQuoteStatuses'),
          store.dispatch('bulkQuote/getBulkQuoteItemInfo', { id: props.quoteId })
        ])
        if (statusResponse?.length) {
          listStatus.value = statusResponse.map(item => ({
            ...item,
            disabled: [9000, 9001].includes(props.status?.id) 
              ? ![9000, 9001].includes(item.id)
              : false,
            active: item.id === props.status?.id
          }))
          selected.value = listStatus.value.find(item => item.id === props.status?.id)
        } else {
          listStatus.value = []
          selected.value = undefined
        }
        isReconciledExist.value = info?.total_reconcile > 0
      } catch {
        listStatus.value = []
        selected.value = undefined
      } finally {
        loading.value = false
        loadingUpdate.value = false
      }
    }
    const handleSelectStatus = (item) => {
      if (item.active || item.disabled) return

      selected.value = item
      listStatus.value = listStatus.value.map(_item => ({
        ..._item,
        active: selected.value.id === _item.id ? true : false
      }))
    }
    const handleCancelUpdate = () => {
      showWarning.value = false
      warningType.value = 0
    }
    const handleSave = () => {
      if (selected.value?.id === props.status?.id || !props.quoteId) return;

      if (
        (props.status?.id === 9001 && selected.value?.id === 9000) || 
        (props.status?.id === 9000 && selected.value?.id === 9001)
      ) {
        warningType.value = props.status?.id === 9000 && selected.value?.id === 9001 ? 0 : 1;
        showWarning.value = true;
        return;
      }
      if (isReconciledExist.value && [9003, 9004].includes(props.status?.id)) {
        if ([9000, 9001, 9002, 2].includes(selected.value?.id)) {
          warningType.value = 1;
          showWarning.value = true;
          return;
        }
      }

      if (isSignedContract && selected.value?.id !== 9000 && selected.value?.id !== 9001) {
        handleUpdateStatus();
        return;
      }
      const warningStatus = [...listStatus.value].slice(
        0,
        listStatus.value.findIndex(item => item.id === props.status?.id)
      );
      const isWarningConditionMet =
        (warningStatus.some(item => item.id === selected.value?.id) && props.order?.is_signature_exist) ||
        ([9004, 3].includes(props.status?.id) && ![9004, 3].includes(selected.value?.id));

      warningType.value = isWarningConditionMet ? 0 : 1;
      showWarning.value = true;
    };

    const handleUpdateStatus = async () => {
      loadingUpdate.value = true
      loading.value = true

      // 3 => FOR COMPLETED
      // 7 => FOR PAYOUT READY/PAYOUT PROCESSING
      if ([3, 7].includes(selected.value?.id)) {
        storePiniaNotes.warningNotes.ids = [props.order?.id]
        !storePiniaNotes.warningNotes.isReviewed && await storePiniaNotes.$getNotesUnread()
        if (!!storePiniaNotes.warningNotes.notes.length) {
          storePiniaNotes.warningNotes.modal = true
          loading.value = false
          loadingUpdate.value = false
          return
        }
      }

      await store.dispatch('bulkQuote/updateQuoteStatus', {
        id: props.quoteId,
        data: {
          status_id: selected.value.id,
          new_step: selected.value.step_bulk
        }
      }).then(() => {
        loadingUpdate.value = false
        loading.value = false
        showWarning.value = false
        ElMessage({
          message: 'Quote status updated successfully.',
          type: 'success'
        })
        ctx.emit('updateStatus', true)
      }).catch(() => {
        loadingUpdate.value = false
        loading.value = false
        showWarning.value = false
      })
      showNotesWarning.value && !!callback && callback()
    }
    watch(() => show.value, async () => {
      if (!!show.value) {
        await handleGetStatus()
      } else {
        listStatus.value = []
      }
    })

    return {
      show,
      selected,
      showWarning,
      warningType,
      loading,
      loadingUpdate,
      listStatus,
      isSignedContract,
      reviewed,
      showNotesWarning,
      notes,
      showNotes,
      handleSave,
      handleCancelUpdate,
      handleUpdateStatus,
      handleSelectStatus
    }
  }
})
</script>